<script lang="coffeescript">

  import {createEventDispatcher, onMount } from "svelte"
  dispatch = createEventDispatcher()

  import Pouch from "./pouch_db.js"
  import SyncSettings from "../Models/SyncSettings.js"




  import {
    PouchSyncSettings
    PouchIsSyncing 
    PouchLastSyncResponse
  } from "./stores.js"
  
  window.createPouchDb = ()->
    window.db = new PouchDB('braintapper-radar', {auto_compaction: true})
    window.pouch = new Pouch window.db

  window.createPouchDb()


  syncSettings = new SyncSettings PouchSyncSettings



  window.testSettings = syncSettings
  window.storeSettings = PouchSyncSettings

  onMount ()->
    console.error "onmount"
    console.log JSON.stringify(syncSettings.pouchify(),null, 2)
    PouchSyncSettings.subscribe  (val)->
      console.log "INIT setting changed"
      console.log val

  window.pouchSync = ()->

    window.pouch.sync()
    .on "change", (response)->
      # handle change
      # console.log "InitPouchDB: Sync: sync changes"
      # console.log response

      response["type"] = "change"
      response["handled"] = false

      PouchLastSyncResponse.set response
      # console.debug response.change.docs


    .on "paused", (err)->
      # replication up to date, user went offline
      # console.log "PouchDB: Sync: paused"
      
      # console.log err
    .on "active", ()->
      # resumed - changes replicating, user went back online
      # console.log "PouchDB: Sync: active"
    .on "denied", (err)->
      # failed to replicate
      # console.log "PouchDB: Sync: sync denied"
      # console.log err
    .on "complete", (response)->
      # handle complete
      # console.log "InitPouchDB: Sync: Complete"
      # console.log response.pull.docs_written # num 
      # console.log response.pull.errors # []
      # console.log response.push.docs_read #num 
      # console.log response.push.errors # []
      ###
        start_time
        end_time
        doc_write_failures
        ok
        last_seq
        status: "complete"
        if response.pull.docs_written > -
      ###

    .on "error", (err)->
      #console.log "PouchDB: Sync: error"
      #console.log error

  window.mounted = ()->
    window.pouch.sync_store = PouchIsSyncing
    window.pouch.notifier = toast
    window.pouchSync()

  toast = (message)->
    showToast = true
    toastMessage = message

  syncCheck = ()->
    doSync = true
    if window.pouch.sync_db
      lastSyncAttempt = localStorage.getItem("radar_last_sync_attempt")
      if lastSyncAttempt?
          minutesSinceLastSync = Date.create().minutesSince(lastSyncAttempt)
          doSync = (minutesSinceLastSync >= 5)
      unless window.pouch.is_syncing
        if doSync
          window.pouchSync()



  syncCheck.every(60000) # minute

</script>