var SettingsDoc, SyncSettings;

SettingsDoc = require("./SettingsDoc.js");

SyncSettings = (function() {
  // This is a local doc, not synced
  class SyncSettings extends SettingsDoc {};

  SyncSettings.prototype.id = "_local/sync";

  SyncSettings.prototype.settings = {
    enabled: false,
    remote_database: null,
    remote_user: null,
    remote_password: null,
    last_sync_attempt: null,
    last_successful_sync: null,
    recent_sync_messages: [],
    auto_sync: true,
    auto_sync_interval: 60000
  };

  return SyncSettings;

}).call(this);

module.exports = SyncSettings;
