var PouchDoc, SettingsDoc;

PouchDoc = require("../PouchDb/pouch_doc.js");

SettingsDoc = (function() {
  // This is a local doc, not synced
  class SettingsDoc extends PouchDoc {
    after_construct(model) {
      var that;
      // model is the writeable store
      this.store = model;
      that = this;
      model.subscribe(function(val) {
        console.error(`SettingsDoc change detected for ${that.id}, initialized=${that.initialized}`);
        if (that.initialized) {
          console.log("VAL:");
          console.log(val);
          that.settings = val;
          return that.save();
        }
      });
      return this.get();
    }

    get() {
      var that;
      that = this;
      return db.get(this.id).then(function(originalDoc) {
        console.log("SettingsDoc.get");
        console.log(that.settings);
        if (that.store != null) {
          
          // since the settings object defaults has new fields, then overrite
          Object.keys(originalDoc.settings).forEach(function(key) {
            console.log(`KEY: ${key}`);
            if (Object.has(that.settings, key)) {
              return that.settings[key] = originalDoc.settings[key];
            } else {
              return console.warn(`skipped [${key}] as it is no longer in spec`);
            }
          });
          that.initialized = true;
          return that.store.set(that.settings);
        } else {
          return console.log("NO STORE FOUND?");
        }
      }).catch(function(err) {
        var settings;
        console.error("console.log "); //{that.id} NOT FOUND"
        console.log(err);
        settings = that.pouchify();
        console.log("settings");
        console.log(settings);
        return db.put(settings).then(function(response) {
          if (that.store != null) {
            that.initialized = true;
            that.store.set(settings);
          } else {
            console.log("CATCH NO STORE FOUND?");
          }
          console.error("WROTE SETTINGS TO DB");
          return console.log(response);
        });
      }).finally;
    }

    save() {
      var that;
      that = this;
      console.error(`SettingsDoc.save, initialized=${this.initialized}`);
      return db.get(this.id).then(function(originalDoc) {
        originalDoc.settings = that.settings;
        return db.put(originalDoc).then(function(response) {
          console.log("--------------updated settings doc");
          console.log(response);
          if (typeof thenCb !== "undefined" && thenCb !== null) {
            return thenCb(response);
          }
        }).catch(function(err) {
          if (typeof catchCb !== "undefined" && catchCb !== null) {
            return catchCb(err);
          }
        });
      }).catch(function(err) {
        var settings;
        console.error("SETTINGS");
        console.log("NOT FOUND");
        console.log(err);
        settings = that.pouchify().settings;
        return db.put(settings).then(function(response) {
          if (that.store != null) {
            that.store.set(settings);
          }
          console.error("WROTE SETTINGS TO DB");
          return console.log(response);
        });
      });
    }

  };

  SettingsDoc.prototype.id = "_local/settings";

  SettingsDoc.prototype.attribute_whitelist = ["id", "settings"];

  SettingsDoc.prototype.initialized = false;

  SettingsDoc.prototype.store = void 0;

  SettingsDoc.prototype.settings = {};

  return SettingsDoc;

}).call(this);

module.exports = SettingsDoc;
